/* eslint-disable @typescript-eslint/no-explicit-any */
import { ServerResponse } from 'http';
import { Hotel } from '../../data-type';

const itemToHotel = (item: any): Hotel => {
  const hotel = {
    ...item,
    host: {
      avatarUrl: item.host.avatar_url,
      id: item.host.id,
      isPro: item.host.is_pro,
      name: item.host.name,
    },
    isFavorite: item.is_favorite,
    isPremium: item.is_premium,
    maxAdults: item.max_adults,
    previewImage: item.preview_image,
  };

  delete hotel.is_favorite;
  delete hotel.is_premium;
  delete hotel.max_adults;
  delete hotel.preview_image;

  return hotel;
};

export const adaptHotelsToClient = (data: ServerResponse): Hotel[] => {
  if (!Array.isArray(data)) {
    throw new Error('–ü—Ä–∏—à–ª–∏ –ø–æ–¥–æ–∑—Ä–∏—Ç–µ–ª—å–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ —Å —Å–µ—Ä–≤–µ—Ä–∞. –≠—Ç–æ –ù–ï —Å–ø–∏—Å–æ–∫ –æ—Ç–µ–ª–µ–π');
  }

  return data.map((item) => itemToHotel(item));
};

export const adaptHotelIdToClient = (data: ServerResponse): Hotel => {
  if (data.constructor.name !== 'Object') {
    throw new Error('–ü—Ä–∏—à–ª–∏ –ø–æ–¥–æ–∑—Ä–∏—Ç–µ–ª—å–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ —Å —Å–µ—Ä–≤–µ—Ä–∞. –≠—Ç–æ –ù–ï –æ—Ç–µ–ª—å');
  }

  return itemToHotel(data);
};
